@import url('./Global.css');
#welcome-intro-container {
    position: absolute;
    color: #fff;
    box-shadow: 0 2px 5px 0 rgb(0 0 0 / 16%), 0 2px 10px 0 rgb(0 0 0 / 12%);
    color: #fff;
    height: 100%;
    width: 100%;
    background: rgba(0,0,0,0.1);
    z-index: 2;
    transition: opacity .5s ease;
    -webkit-transition: opacity .5s ease;
    -moz-transition: opacity .5s ease;
    -ms-transition: opacity .5s ease;
    -o-transition: opacity .5s ease;
    will-change: auto;
}
#welcome-intro-container.show {
    visibility: visible;
    opacity: 1;
}
#welcome-intro-container.hide {
    visibility: hidden;
    opacity: 0;
}

#welcome-name-container {
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    height: 75%;
    padding: 0 3rem;
}

#welcome-name-placeholder-block-left {
    flex: 1;
}
#welcome-name {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    transition: all 0.3s ease;
    -webkit-transition: all 0.3s ease;
    -moz-transition: all 0.3s ease;
    -ms-transition: all 0.3s ease;
    -o-transition: all 0.3s ease;
    will-change: auto;
    height: 100%;
    transform: translateY(50%);
    -webkit-transform: translateY(50%);
    -moz-transform: translateY(50%);
    -ms-transform: translateY(50%);
    -o-transform: translateY(50%);
    box-shadow: none;
    border-radius:none;
    -moz-border-radius:none;
    -ms-border-radius:none;
    -o-border-radius:none;
    -webkit-border-radius:none;
}
#welcome-name.show {
    box-shadow: 0 0 10px rgb(0 0 0 / 50%);
    border-radius: 1rem;
    backdrop-filter: blur(10px);
    background: rgb(255, 255, 255, 0.4);
    transform: translateY(25%);
    -webkit-transform: translateY(25%);
    -moz-transform: translateY(25%);
    -ms-transform: translateY(25%);
    -o-transform: translateY(25%);
    z-index: 6;
    margin-bottom: 5.5rem;
}
@media screen and (max-width: 1000px) {
    #welcome-name-placeholder-block-left {
        flex: 0.7;
    }
}
@media screen and (max-width: 950px) {
    #welcome-name-placeholder-block-left {
        flex: 0.5;
    }
}
@media screen and (max-width: 850px) {
    #welcome-name-placeholder-block-left {
        flex: 0.3;
    }
}

@media screen and (max-width: 720px) {
    #welcome-name-placeholder-block-left {
        flex: 0;
    }
    #welcome-name {
        flex: 1;
    }
}

#welcome-name em {
    display: block;
    opacity: 1;
    font-size: 1.3rem;
    font-weight: 500;
    padding-bottom: 1rem;
    transition: all .3s ease;
    -webkit-transition: all .3s ease;
    -moz-transition: all .3s ease;
    -ms-transition: all .3s ease;
    -o-transition: all .3s ease;
    will-change: auto;
}
#welcome-name.show em {
    display: none;
    opacity: 0;
}
#welcome-name h1 span {
    font-size: 0.9rem;
    font-weight: lighter;
}

#name-container {
    margin: 0;
    font-size: 2.8rem;
    padding: 0.5rem 0.7rem;
    width: max-content;
    height: max-content;
    backdrop-filter: blur(10px);
    cursor: pointer;
    color: var(--description-green);
    overflow: hidden;
    background: rgb(255, 255, 255, 0.3);
    box-shadow: 0 0 10px;
    transition: all .3s linear;
    border-radius: 0.5rem;
    -webkit-transition: all .3s linear;
    -moz-transition: all .3s linear;
    -ms-transition: all .3s linear;
    -o-transition: all .3s linear;
    will-change: auto;
}

#name-container:focus { 
    outline: none; 
}

#name-container:hover {
    backdrop-filter: blur(0);
    color: white;
    box-shadow: none;
}
#welcome-name.show #name-container {
    backdrop-filter: blur(0);
    background-color: transparent;
    box-shadow: none;
    font-size: 2rem;
    cursor: default;
    /* color: white; */
}
#welcome-name.show #name-container:hover{
    color: var(--description-green);
}

#profile-container {
    padding: 1rem;
    box-sizing: border-box;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    visibility: hidden;
    opacity: 0;
    transition: all .3s ease;
    -webkit-transition: all .3s ease;
    -moz-transition: all .3s ease;
    -ms-transition: all .3s ease;
    -o-transition: all .3s ease;
    will-change: auto;
    color: black;
}

#profile-container.show {
    visibility: visible;
    opacity: 1;
    width: 100%;
    height: 100%;
}
#profile-container h5 {
    line-height: 2rem;
    padding: 1rem 0;
    font-size: 1rem;
    font-weight: normal;
    border-top: 2px solid;
    border-image-source: linear-gradient(to left, transparent, transparent, var(--description-green), transparent ,transparent);
    border-image-slice: 1;
}
#profile-container ul {
    list-style: none;
}
.basic-info {
    padding: .5rem 0;
    display: flex;
    flex-direction: row;
    align-items: center;
}
.basic-info:before {
    content: "";
    margin-right: 1rem;
    display: inline-block;
    width: 1.5rem;
    height: 1.5rem;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
}
#email.basic-info::before {
    background-image: url("../images/email.svg");
}
#location.basic-info::before {
    background-image: url("../images/location.svg");
}

.other-link svg{
    cursor: pointer;
    fill: white;
    border-radius: 24px;
    box-shadow: 0 0 10px rgb(0, 0, 0, 0.2);
}
.other-link svg:hover{
    fill: var(--description-green);
    box-shadow: 0 0 10px var(--description-green);
    filter: drop-shadow( 3px 3px 2px rgba(0, 0, 0, 0.3));
    -webkit-filter: drop-shadow( 3px 3px 2px rgba(0, 0, 0, 0.3));
}

#other-link-container {
    display: flex;
    flex-direction: row;
    width: 200px;
    justify-content: space-between;
    border-image-slice: 1;
}

#basic-info-container {
    width: 100%;
    padding-bottom: 2rem;
    box-sizing: border-box;
}
#close-profile{
    outline: none;
    background-image: url("../images/close.svg");
    width: 2rem;
    height: 2rem;
    cursor: pointer;
    transition: transform 0.2s ease;
    -webkit-transition: transform 0.2s ease;
    -moz-transition: transform 0.2s ease;
    -ms-transition: transform 0.2s ease;
    -o-transition: transform 0.2s ease;
    will-change: auto;
}
#close-profile:hover {
    transform: scale(1.1);
    -webkit-transform: scale(1.1);
    -moz-transform: scale(1.1);
    -ms-transform: scale(1.1);
    -o-transform: scale(1.1);
}
#profile {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
}